import caifuBottom from '../components/caifuBottom'
export default {
    name: 'loginAfter',
    data() {
        return {
            error: false,
            errorMsg: "请输入11个字符以内文字/字母/数字",
            fundName: '',
            loginAfterShow:false
        }
    },
    computed: {

    },
    components: {
        caifuBottom
    },
    mounted() {
            this.intUser();
    },
    methods: {
        sure() {
            if (!/^[a-zA-Z0-9\u4e00-\u9fa5\*]+$/.test(this.fundName)) {
                this.error = true;
                return;
            } else {
                this.error = false;
                this.setName();
            }
        },
        setName() { //设置效验组合名称
            let _this = this;
            this.post({
                url: "/fund/setFundName/v1.0",
                data: {
                    fundName: _this.fundName
                },
                success: function(e) {
                    if (e.code == "0000") {
                         sessionStorage.setItem('flag', 'tiao');
                        if (sessionStorage.getItem('myAccount')) {
                            _this.$router.push('/myAccount');
                            sessionStorage.removeItem('myAccount');
                        } else {
                            _this.$router.push('/fundPortfolio'); //已有基金组合名
                        }
                    }else if (e.code == "1002") {
                        _this.errorMsg = "基金组合名已被占用";
                        _this.error = true;
                    } else {
                        _this.errorMsg = "请输入11个字符以内文字/字母/数字"
                        _this.error = false;
                    }
                }
            })
        },
        intUser() {
            let _this = this;
            this.post({
                url: "/fund/initAcct/v1.0",
                success: function(e) {
                    if (e.code == "0000") {
                        let _str = e.result.loginName.substr(0, 3),
                           _str1=e.result.loginName.substr(7,11);
                        if (e.result.isHaveFundName == 1) {
                            if (!sessionStorage.getItem('flag')) {
                                sessionStorage.setItem('flag', 'tiao');
                                if (sessionStorage.getItem('myAccount')) {
                                    _this.$router.push('/myAccount');
                                    sessionStorage.removeItem('myAccount');
                                } else {
                                    _this.$router.push('/fundPortfolio'); //已有基金组合名
                                }
                            }else{
                                if(sessionStorage.getItem('goAppShare1') && sessionStorage.getItem('goAppShareUrl')){//如果有分享flag
                                    sessionStorage.removeItem('goAppShare1');
                                    window.location.href= sessionStorage.getItem('goAppShareUrl');
                                }else{
                                   window.location.replace(window.location.origin+"/p/fund.html#/index");
                                    //  _this.$router.push('/index');
                                }
                            }
                        } else {
                            _this.loginAfterShow=true;
                            _this.fundName = _str+'****'+_str1;
                        }
                    }
                }
            })
        }
    }
}



